.Landing {
  padding: 200px;
}

.big-logo {
  width: 500px;
  height: 300px;
  display: block;
  margin: auto;
  animation: fadein 7s;
  -moz-animation: fadein 7s;
  /* Firefox */
  -webkit-animation: fadein 7s;
  /* Safari and Chrome */
  -o-animation: fadein 7s;
  /* Opera */
}

.big-logo:hover {
  cursor: pointer;
}

@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@-webkit-keyframes fadein {
  /* Safari and Chrome */
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}



