.App {
  background-image: -webkit-gradient(linear, left bottom, right top, from(#845EC2), color-stop(#D65DB1), color-stop(#FF6F91), color-stop(#FF9671), color-stop(#FFC75F), to(#F9F871));
  background-image: -webkit-linear-gradient(left bottom, #845EC2, #D65DB1, #FF6F91, #FF9671, #FFC75F, #F9F871);
  background-image: -o-linear-gradient(left bottom, #845EC2, #D65DB1, #FF6F91, #FF9671, #FFC75F, #F9F871);
  background-image: linear-gradient(to right top, #845EC2, #D65DB1, #FF6F91, #FF9671, #FFC75F, #F9F871);
  background-position: absolute;
  background-repeat: no-repeat;
  background-size: cover;
}


